const MyBanner = () => {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectIndex, e) => {
    setIndex(selectIndex);
  };

  return (
    <Carousel
      interval={3000}
      slide={true}
      touch={true}
      pauseonhover="false"
      controls={false}
      activeIndex={index}
      onSelect={handleSelect}
    >
      {data.Banner.map((res, index) => (
        <Carousel.Item key={`banner${index}`}>
          <img
            className="d-block w-100 bannerSize"
            src={`${res.img}`}
            alt={`${res.title}`}
          />
          <Carousel.Caption>
            <h3>{`${res.title}`}</h3>
            <p>{`${res.desc}`}</p>
          </Carousel.Caption>
        </Carousel.Item>
      ))}
    </Carousel>
  );
};
